.search-form {
  background: linear-gradient(to right, #0077c2, #ffd400);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.search-form button[type="submit"]:hover, .search-form button[type="submit"]:focus {
  background-color: #0056b3;
}

button:hover, button:focus {
  background-color: #0056b3;
}

